/* Gradient Background */
body {
  background: linear-gradient(135deg, #ffffff 0%, #ffffff 100%);
  font-family: "Arial", sans-serif;
  color: #ffffff;
  line-height: 1.6;
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

/* General Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


/* App Container */
.app {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

/* Header */
.header {
  text-align: center;
  margin-bottom: 40px;
}

.header h1 {
  font-size: 2.5rem;
  color: #2c3e50;
  margin-bottom: 10px;
}

.tagline {
  font-size: 1.2rem;
  color: #e74c3c;
  margin-bottom: 20px;
  font-style: italic;
}

/* Search Bar */
.search-bar {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-bottom: 20px;
}

.search-bar input {
  width: 300px;
  padding: 10px;
  border: 2px solid #ddd;
  border-radius: 5px;
  font-size: 1rem;
  outline: none;
  transition: border-color 0.3s ease;
  background-color: white;
  color: #333;
}

.search-bar input:focus {
  border-color: #3498db;
}

.search-bar button {
  padding: 10px 20px;
  background-color: #3498db;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.search-bar button:hover {
  background-color: #2980b9;
}

/* Error Message */
.error {
  color: #e74c3c;
  text-align: center;
  margin-top: 10px;
}

/* Recipe Grid */
.recipe-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  padding: 20px;
}

/* Recipe Card */
.recipe-card {
  background: white;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  animation: fadeIn 0.5s ease-in-out;
}

.recipe-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.recipe-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.recipe-info {
  padding: 15px;
  text-align: center;
}

.recipe-info h2 {
  font-size: 1.25rem;
  margin-bottom: 10px;
  color: #2c3e50;
}

.recipe-info p {
  font-size: 0.9rem;
  color: #7f8c8d;
  margin-bottom: 15px;
}

.recipe-info a {
  display: inline-block;
  padding: 8px 16px;
  background-color: #3498db;
  color: white;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.recipe-info a:hover {
  background-color: #2980b9;
  transform: scale(1.05);
}

/* Fade-in Animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}